<template>
  <div>
    <div
      style="font-family:Titillium Web;line-height: 1.0;background:black;"
      class="p-3 ml-1 mr-1 bg-transparent shadow-lg rounded-lg animate__animated animate__fadeIn"
    >
    <router-link to="/projectadd">
      <button
        type="button"
        @click="$router.push('/projectadd')"
        class="float-right btn btn-sm btn-style9 float-right"
        style="margin-left:5px;padding:3px;padding-top:0px;padding-bottom:0px;background: rgb(11,1,1);
background: linear-gradient(90deg, rgba(11,1,1,0.7203256302521008) 3%, rgba(6,1,1,0.7791491596638656) 99%);"
      >
        <span class="text-2xl typcn typcn-coffee"></span>
      </button>
    </router-link>
      <div class="row">
        <div class="col-6">
          <input
            type="text"
            id="search"
            name="search"
            class="form-control form-control-sm"
            placeholder="search"
            v-model="searchItem"
          />
        </div>
        <div class="col-12">
          <button type="button" @click="pilih=''" class="btn btn-sm btn-style9 text-black p-1">-ALL-</button>
          <button type="button" @click="pilih='WEB'" class="btn btn-sm btn-style9 text-black p-1">WEB</button>
          <button type="button" @click="pilih='ANDROID/IOS'" class="btn btn-sm btn-style9 text-black p-1">ANDROID</button>
          <button type="button" @click="pilih='DESKTOP'" class="btn btn-sm btn-style9 text-black p-1">DESKTOP</button>
          <button type="button" @click="pilih='AUTOMATION'" class="btn btn-sm btn-style9 text-black p-1">AUTOMATION</button>
          <button type="button" @click="pilih='OTHER'" class="btn btn-sm btn-style9 text-black p-1">OTHER</button>
        </div>
      </div>
      <div class="row" v-if="filteredData.length>0">
        <router-link :to="`/${item.id}`" tag="div" class="col-sm-6" v-for="(item, index) in filteredData" :key="index">
          <div
            class="shadow-lg  rounded-lg p-2 mt-3 anim hover:bg-yellow-200 cursor-pointer"
            style="min-height:380px;box-shadow:2px 2px 6px 2px;"
          >
            <div class="text-center p-3">
              <img
                :src="item.gambar"
                :key="item.gambar"
                class="mx-auto kinoLightBox img-fluid rounded-lg shadow-lg"
                style="height:250px;"
              />
            </div>

            <p class="pl-2 font-bold text-capitalize" style="font-size:14px;">{{item.judul}}</p>
          </div>
        </router-link>
      </div>
      <div class="container-fluid">
        <div class="row">
          <div class="col-sm-12">
            <br />
            <br />
            <button
              type="button"
              v-for="(item, index) in tombolPagination"
              @click="changePagination(item)"
              :key="index+'pagination2'"
              v-show="item<jumlahPagination+1"
              class="btn btn-sm btn-warning rounded-circle"
              :class="{'bg-blue hover:bg-blue border-blue cursor-not-allowed text-red':item==pagination}"
              :disabled="item==pagination"
            >{{item}}</button>
          </div>
        </div>
      </div>
      <div class="float-right">
        <button
          type="button"
          @click="pagPrev"
          style="background: rgb(11,1,1);
background: linear-gradient(90deg, rgba(11,1,1,0.7203256302521008) 3%, rgba(6,1,1,0.7791491596638656) 99%);"
          class="btn btn-sm btn-primary"
        >Previous</button>
        <button
          type="button"
          @click="pagNext"
          style="background: rgb(11,1,1);
background: linear-gradient(90deg, rgba(11,1,1,0.7203256302521008) 3%, rgba(6,1,1,0.7791491596638656) 99%);"
          class="btn btn-sm btn-primary"
        >Next</button>
      </div>
    </div>
    <h1 style="font-size:1px;opacity:0.1;">Project TA_Maliki / Taufik Akbar Maliki my project PORTOFOLIO WEBSITE OFFICIAL</h1>
  </div>
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
// let rdb = firebase.database()
import _ from 'lodash'
import autophp from '@/plugins/upload_autophp'
// let db = firebase.firestore()
let sdb = new autophp()
export default {
  data () {
    return {
      datanya: '',
      pilih: '',
      temp1: 0,
      temp2: 10,
      pagination: 1,
      pembagiannya: 10,
      orderBy: '',
      orderWith: true,
      searchItem: ''
    }
  },
  layout: 'author',
  computed: {
    jumlahPagination () {
      let data = this.datanya
      return data.length / 10
    },
    tombolPagination () {
      let data = this.pagination
      // v-if != (current > max ) button nya
      let btn1 = data - 2
      let btn2 = data - 1
      let btn3 = data
      let btn4 = data + 1
      let btn5 = data + 2
      let array = [btn1, btn2, btn3, btn4, btn5]
      if (array.indexOf(-1) != -1) {
        array = [1, 2, 3, 4, 5]
      } else if (array.indexOf(0) != -1) {
        array = [1, 2, 3, 4, 5]
      }
      return array
    },
    filteredData () {
      let that = this
      let data = this.datanya
      //! order fungsi
      if (this.orderBy.length > 0) {
        data = this.$_.orderBy(data, ['title'], [this.orderBy])
      }
      //! search fungsi
      if (data[0] != undefined) {
        let keys = Object.keys(data[0])
        data = data.filter((v, i, a) => {
          let hasil = false
          keys.filter((key) => {
            if (typeof v[key] === 'string') {
              if (
                v[key].toLowerCase().indexOf(this.searchItem.toLowerCase()) !=
                -1
              ) {
                hasil = true
              }
            } else {
              if (v[key] != undefined) {
                if (v[key].toString().indexOf(this.searchItem) != -1) {
                  hasil = true
                }
              }
            }
          })
          if (hasil) return v
        })
        data = data.slice(this.temp1, this.temp2)
        data = data.filter((v, i, a) => {
          let hasil = false
          console.log(v.type == that.pilih)
          if (v['type'] == this.pilih) {
            return v
          } else if (this.pilih == '') {
            return v
          }
        })
      }
          data=data.map(e=>{
            return {
              ...e,
              urutan:parseInt(e.urutan)
            }
          })
          data = _.orderBy(data, 'urutan', 'asc'); 
      // data = data.map(e => {
      //   let obj = {};
      //   let kunci = Object.keys(e);
      //   kunci = that.$_.difference(kunci, that.unless);
      //   kunci.forEach((a, index) => {
      //     if (kunci.find(e => e == that.date[index])) {
      //     } else {
      //       // obj[a] = 'tanggal'
      //     }
      //     console.log(typeof e[a]);
      //     obj[a] =
      //       typeof e[a] != "string"
      //         ? e[a]
      //         : new Date(e[a]) !== "Invalid Date" && !isNaN(new Date(e[a]))
      //         ? that.format(e[a])
      //         : e[a];
      //   });
      //   return obj;
      // });

      return data
    }
  },
  methods: {
    pagPrev () {
      this.pagination = this.pagination - 1
      if (this.pagination == 0) {
        this.pagination = 1
      }
      this.temp2 = this.pagination * this.pembagiannya
      this.temp1 = Math.ceil(this.temp2) - this.pembagiannya
    },
    pagNext () {
      this.pagination = this.pagination + 1
      if (this.pagination > this.jumlahPagination + 1) {
        this.pagination = Math.ceil(this.jumlahPagination)
      }
      this.temp2 = this.pagination * this.pembagiannya
      this.temp1 = Math.ceil(this.temp2) - this.pembagiannya
    },
    changePagination (pag) {
      this.pagination = pag
      this.temp2 = this.pagination * this.pembagiannya
      this.temp2 = Math.ceil(this.temp2)
      this.temp1 = this.temp2 - this.pembagiannya
      this.temp1 = Math.ceil(this.temp1)
    }
  },
  mounted () {
    // db.collection('tamaliki')
    //   .get()
    //   .then((res) => {
    //     let data = res.docs.map((e) => {
    //       return {
    //         id: e.id,
    //         ...e.data()
    //       }
    //     })
    //     this.datanya = data
    //   })
      sdb.collection("portofolio").doc().select(`select * from portofolio`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
      
  },
  metaInfo () {
    return {

      title: 'Project TA_Maliki / Taufik Akbar Maliki my project PORTOFOLIO WEBSITE OFFICIAL',
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: 'Project TA_Maliki / Taufik Akbar Maliki my project PORTOFOLIO WEBSITE OFFICIAL'
        },
        {
          property: 'og:title',
          content: 'Project TA_Maliki / Taufik Akbar Maliki my project PORTOFOLIO WEBSITE OFFICIAL'
        },
        { property: 'og:site_name', content: 'TA_Maliki' },
        { property: 'og:type', content: 'website' },
        {
          property: `og:url`,
          content: `https://tamalik.vercel.app/`
        },
        {
          property: `og:image`,
          content: `https://firebasestorage.googleapis.com/v0/b/taufikakbarmaliki-bba49.appspot.com/o/download.png?alt=media&token=87ccdaaa-d68f-4b67-9394-e1b5738f3920`
        },
        {
          property: `og:description`,
          content: `Project TA_Maliki / Taufik Akbar Maliki my project PORTOFOLIO WEBSITE OFFICIAL`
        },
        {
          name: 'keywords',
          content: 'TA_Maliki,project,my project,Taufik Akbar Maliki'
        },
        { name: 'robots', content: 'index,follow' }
      ]
    }
  }
}
</script>
